<template>
  <v-dialog :value="dialog" persistent scrollable :fullscreen="$vuetify.breakpoint.xsOnly" max-width="600px" @keydown.esc="cancel()">
    <v-card>
      <v-card-title>
        <span class="headline" v-text="$t('user')"/>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field :label="$t('first name')" v-model="item.firstName" required/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field :label="$t('last name')" v-model="item.lastName" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field :label="$t('email')" v-model="item.email" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field :label="$t('password')" v-model="item.password" type="password" required/>
            </v-flex>
            <v-flex xs12>
              <v-select :label="$t('role')" :items="roles" v-model="item.role" required/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">{{ $t('cancel') }}</v-btn>
        <v-btn color="primary" flat @click.native="save()">{{ $t('save') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Object,
    dialog: Boolean,
    button: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    item: {},
    rawRoles: [1, 2, 3, 4].map(value => ({
      text: `roles.${value}`,
      value
    }))
  }),
  computed: {
    roles () {
      return this.rawRoles.map(role => ({
        ...role,
        text: this.$t(role.text)
      }))
    }
  },
  watch: {
    dialog (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
      this.$parent.$emit('cancel')
    },
    save () {
      this.$emit('input', this.item)
      this.$parent.$emit('input', this.item)
    }
  }
}
</script>
