<template>
  <v-container :class="{ 'px-0': $vuetify.breakpoint.mdAndDown }">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <CTable :headers="headers" :items="items" @save="save" @remove="remove">
          <template slot-scope="props">
            <CarDialog :dialog="props.dialog" :value="props.value"/>
          </template>
        </CTable>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { fetchAllCars } from '@/axios'

import CarDialog from '@/components/CarDialog'
import CTable from '@/components/Table'

export default {
  components: { CTable, CarDialog },
  data: () => ({
    headers: [
      { text: 'name', value: 'name' },
      { text: 'owner', value: 'owner' },
      { text: 'certification', value: 'certification', i18n: 'switch' },
      { text: 'last revision', value: 'lastRevision' },
      { text: 'last ZTE', value: 'lastZTE' },
      { text: 'last ZTL', value: 'lastZTL' }
    ],
    items: []
  }),
  methods: {
    save (item) {
      console.log('send', 'put', item)
    },
    remove (item) {
      console.log('send', 'delete', item)
    },
    async load () {
      this.items = []
      try {
        this.items = await fetchAllCars()
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
