import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    role: null,
    working: false,
    alert: {
      show: false,
      text: '',
      type: 'info'
    }
  },
  mutations: {
    setRole (state, role) {
      state.role = role
    },
    setWorking (state, working) {
      state.working = working
    },
    setAlert (state, { type, text }) {
      state.alert = {
        show: true,
        type,
        text
      }
    },
    clearAlert (state) {
      state.alert.show = false
    }
  },
  getters: {
    allowedRouteNames: state => {
      const common = ['logout', 'home']
      switch (state.role) {
        case 'admin':return [
          ...common,
          'cars', 'administration'
        ]
        case 'externalUser':return [
          ...common,
          'home'
        ]
        case 'manager':return [
          ...common,
          'cars'
        ]
        case 'serviceTechnician':return [
          ...common
        ]
        default:return [
          'login'
        ]
      }
    }
  }
})
