<template>
  <v-app>
    <v-toolbar app extension-height="7px">
      <v-toolbar-title class="headline text-uppercase">
        <span class="accent--text">KST</span>
        <span class="primary--text font-weight-light">INPRO</span>
      </v-toolbar-title>

      <v-spacer/>

      <v-btn class="hidden-sm-and-down" v-for="({text, to}, i) in items" v-text="text" :key="i" :to="to" flat color="primary"/>
      <v-menu class="hidden-md-and-up" bottom left>
        <v-btn slot="activator" dark icon>
          <v-icon color="accent">mdi-dots-vertical</v-icon>
        </v-btn>

        <v-list>
          <v-list-tile v-for="({text, to}, i) in items" :key="i">
            <v-list-tile-content>
              <v-btn v-text="text" :to="to" block flat color="primary" style="white-space: nowrap;"/>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-menu>

      <v-progress-linear
        v-show="progress.show"
        :indeterminate="progress.indeterminate === true"
        :value="progress.value"
        class="ma-0"
        slot="extension"
        color="accent"
      />
    </v-toolbar>

    <v-content>
      <v-alert v-model="alert.show" :type="alert.type" dismissible class="mt-0" transition="fade-transition">
        {{ alert.text }}
      </v-alert>
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    rawItems: [
      { text: 'home', to: { name: 'home' } },
      { text: 'cars', to: { name: 'cars' } },
      { text: 'administration', to: { name: 'administration' } },
      { text: 'login', to: { name: 'login' } },
      { text: 'logout', to: { name: 'logout' } }
    ]
  }),
  computed: {
    allowedRouteNames () {
      return this.$store.getters.allowedRouteNames
    },
    items () {
      return this.rawItems.filter(item =>
        this.allowedRouteNames.includes(item.to.name)
      ).map(item => ({
        ...item,
        text: this.$t(item.text)
      }))
    },
    progress () {
      const working = this.$store.state.working
      return {
        show: working !== false,
        indeterminate: working === true,
        value: typeof working === 'number' ? working : 0
      }
    },
    alert () {
      return this.$store.state.alert
    }
  }
}
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}

.v-toolbar__extension {padding: 0px;}
.v-dialog--scrollable.v-dialog--fullscreen > .v-card > * {
  flex-grow: 0;
}
.v-dialog--scrollable.v-dialog--fullscreen > .v-card > .v-card__text {
  flex-grow: 1;
}
</style>
