<template>
  <v-container :class="{ 'px-0': $vuetify.breakpoint.mdAndDown }">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <CTable :headers="headers" :items="items" @save="save" @remove="remove">
          <template slot-scope="props">
            <UserDialog :dialog="props.dialog" :value="props.value"/>
          </template>
        </CTable>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { fetchAllUsers } from '@/axios'

import UserDialog from '@/components/UserDialog'
import CTable from '@/components/Table'

export default {
  components: { CTable, UserDialog },
  data: () => ({
    headers: [
      { text: 'first name', value: 'firstName' },
      { text: 'last name', value: 'lastName' },
      { text: 'email', value: 'email' },
      { text: 'role', value: 'role', i18n: 'roles' }
    ],
    items: []
  }),
  methods: {
    save (item) {
      console.log('send', 'put', item)
    },
    remove (item) {
      console.log('send', 'delete', item)
    },
    async load () {
      this.items = []
      try {
        this.items = await fetchAllUsers()
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
