<template>
  <v-dialog :value="dialog" persistent scrollable :fullscreen="$vuetify.breakpoint.xsOnly" max-width="600px" @keydown.esc="cancel()">
    <v-card>
      <v-card-title>
        <span class="headline" v-text="$t('car')"/>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12>
              <v-text-field :label="$t('name')" v-model="item.name" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field :label="$t('owner')" v-model="item.owner" required/>
            </v-flex>
            <v-flex xs12>
              <v-switch :label="$t('certification')" v-model="item.certification" required/>
            </v-flex>
            <v-flex xs12>
              <DateTime :label="$t('last revision')" v-model="item.lastRevision"/>
            </v-flex>
            <v-flex xs12>
              <DateTime :label="$t('last ZTE')" v-model="item.lastZTE"/>
            </v-flex>
            <v-flex xs12>
              <DateTime :label="$t('last ZTL')" v-model="item.lastZTL"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">{{ $t('cancel') }}</v-btn>
        <v-btn color="primary" flat @click.native="save()">{{ $t('save') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import DateTime from '@/components/DateTime'

export default {
  components: { DateTime },
  props: {
    value: Object,
    dialog: Boolean,
    button: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    item: {}
  }),
  watch: {
    dialog (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
      this.$parent.$emit('cancel')
    },
    save () {
      this.$emit('input', this.item)
      this.$parent.$emit('input', this.item)
    }
  }
}
</script>
