<template>
  <v-container style="max-width: 40em;">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <h1 class="display-2 font-weight-bold mb-3" v-text="$t('login')"/>
      </v-flex>
      <v-flex xs12>
        <v-text-field :label="$t('username')" v-model="username"/>
      </v-flex>
      <v-flex xs12>
        <v-text-field :label="$t('password')" v-model="password" type="password"/>
      </v-flex>
      <v-flex xs12>
        <v-btn color="primary" v-text="$t('log me in')" @click="login"/>
      </v-flex>
      <v-flex xs12>
        <span v-text="error"/>
      </v-flex>
      <v-flex xs12>
        <pre v-text="debugLogins"/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    username: null,
    password: null,
    error: '',
    debugLogins: [
      'admin - admin',
      'externalUser - externalUser',
      'manager - manager',
      'serviceTechnician - serviceTechnician'
    ].join('\n')
  }),
  methods: {
    login () {
      this.error = ''
      if (this.username === this.password) {
        this.$store.commit('setRole', this.username)
        this.$router.push({ name: 'home' })
      } else {
        this.error = this.$t('logging in failed')
      }
    }
  }
}
</script>

