<template>
  <v-menu
    ref="menu"
    :close-on-content-click="false"
    v-model="menu"
    lazy
    transition="scale-transition"
    offset-y
    full-width
    max-width="290px"
    min-width="290px"
  >
    <v-text-field
      slot="activator"
      v-model="date"
      :label="label"
      readonly
    />
    <v-date-picker
      v-model="date"
      type="date"
      no-title
      scrollable
    >
      <v-spacer/>
      <v-btn flat color="primary" @click="menu = false" v-text="$t('cancel')"/>
      <v-btn flat color="primary" @click="save" v-text="$t('ok')"/>
    </v-date-picker>
  </v-menu>
</template>

<script>
export default {
  props: {
    value: String,
    label: String
  },
  data: () => ({
    item: '',
    menu: false
  }),
  computed: {
    date: {
      get () {
        return this.item.substr(0, 10)
      },
      set (val) {
        this.item = val + this.item.substr(10)
      }
    }
  },
  watch: {
    value (val) {
      this.item = val || ''
    }
  },
  methods: {
    save () {
      this.$emit('input', this.item)
      this.menu = false
    }
  },
  mounted () {
    this.item = this.value || ''
  }
}
</script>
