import Axios from 'axios'
import store from '@/store'

function deCSharpKeys (data) {
  if (data instanceof Array) {
    return data.map(val => deCSharpKeys(val))
  } else if (data instanceof Object) {
    const fixed = {}
    Object.keys(data).forEach(key => {
      fixed[`${key.slice(0, 1).toLowerCase()}${key.slice(1)}`] = deCSharpKeys(data[key])
    })
    return fixed
  } else {
    return data
  }
}

async function download (type, url) {
  try {
    store.commit('setWorking', true)
    store.commit('clearAlert')
    return await axios[type](url)
  } catch (error) {
    store.commit('setAlert', {
      type: 'error',
      text: error.message
    })
    throw error
  } finally {
    store.commit('setWorking', false)
  }
}

const axios = Axios.create({
  baseURL: 'https://inpro-evidencenaklvozu.azurewebsites.net/API/'
})

axios.interceptors.response.use((response) => {
  return {
    ...response,
    data: deCSharpKeys(response.data)
  }
}, error => {
  return Promise.reject(error)
})

async function fetchAllCars () {
  const { data } = await download('get', '/Car/GetAllCars')
  return data
}

async function fetchAllUsers () {
  const { data } = await download('get', '/User/GetAllUsers')
  data.forEach(user => {
    user.role = user.role.roleId
  })
  return data
}

export default axios
export { fetchAllCars, fetchAllUsers }
